<template>
	<div class="ymt-input">
		<span>{{title}}</span>
		<input 
			type="text" 
			ref="input" 
			:placeholder="place" 
			:value="currentValue"   
			:disabled="disabled"  
			@blur="handleBlur"
			@focus="handleFocus" 
			@change="handleChange">
	</div>
</template>

<script type="text/javascript">
	import Vue from 'vue'
	export default Vue.component('ymt-input',{
		name:'YmtInput',
		props:['title','placeholder','value'],
		data(){   
			return {
				place:this._props.placeholder,
				currentValue:this._props.value,
				disabled: this.$vnode.data.attrs.hasOwnProperty("disabled"), 
			}
		},
		methods:{
			handleBlur() {
				if (!!this._events['blur']){
					console.log('ymt-input blur');

					this.$emit('blur',this.$refs.input.value);
				}
			},
			handleFocus() {
				if (!!this._events['focus']){
					console.log('ymt-input focus');

					this.$emit('focus',this.$refs.input.value);
				}
			},
			handleChange() {
				if (!!this._events['change']){
					console.log('ymt-input change');

					this.$emit('change',this.$refs.input.value);
				}
			}
		}
	})
</script>



