<!-- 通用组件 -->
<template>
	<button 
		@click="handleClick" 
		class="ymt-btn ymt-btn-default">
		<slot></slot>
	</button>
</template>

<script type="text/javascript">
	import Vue from 'vue'

	export default Vue.component('ymt-button',{
		name:'YmtButton', 
		methods:{
			handleClick(){
				if (!!this._events["click"]){
					console.log('handleClick');
					// todo
					this.$emit('click');				
				} 
			}
		}
	})
</script>




